/** - w */
export function channelsUpper (params) {
  return new Promise((resolve, reject) => {
    loadRequest('/api/live/web/middleground/utopaChannel/batchUpper', [params.id], '', http, 'post').then(res => {
      if (res.code) {
        Message.error(res.msg)
      } else {
        resolve(res)
      }
    }).catch(err => {
      reject(params.state)
    })
  })
}
