<template>
    <div>
      <el-form ref="form" :model="form" :rules="rules" label-width="150px" style="width: 50%">
        <el-form-item label="" prop="startTime">
            <el-date-picker type="datetime" placeholder="" v-model="form.startTime" style="width: 100%;" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="endTime">
          <el-date-picker type="datetime" placeholder="" v-model="form.endTime" style="width: 100%;" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="liveNetRule">
          <el-radio-group v-model="form.liveNetRule">
            <el-radio :label="1">+</el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="isNeedPassword">
          <el-radio-group v-model="form.isNeedPassword">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
          <span v-if="form.isNeedPassword==0"><el-input v-model.trim="form.livePassword" placeholder="6" style="width: 180px;margin-left: 10px" maxlength="6"></el-input></span>
        </el-form-item>
<!--        <el-form-item label="" prop="isPlayBack">-->
<!--          <el-radio-group v-model="form.isPlayBack">-->
<!--&lt;!&ndash;            <el-radio :label="1"></el-radio>&ndash;&gt;-->
<!--            <el-radio :label="0"></el-radio>-->
<!--          </el-radio-group>-->
<!--        </el-form-item>-->

        <el-form-item>
          <el-button type="primary" @click="updateLiveType('form')"></el-button>
        </el-form-item>
      </el-form>
    </div>
</template>

<script>
  import {getLiveType,updateLiveType} from "../../http/request";

  export default {
        name: "liveType",
        data() {
          return {
            form: {
              startTime:'',
              endTime:'',
              liveNetRule:1,
              isNeedPassword:1,
              livePassword:'',
              isPlayBack:1,
              id:''
            },
            rules:{
              startTime: [{ required: true, message: '', trigger: 'blur' }],
              endTime:[{ required: true, message: '', trigger: 'blur' }],
              liveNetRule:[{ required: true, message: '', trigger: 'blur' }],
              isNeedPassword:[{ required: true, message: '', trigger: 'blur' }],
              isPlayBack:[{ required: true, message: '', trigger: 'blur' }]
            },
            id:''
          }
        },
        methods: {
          updateLiveType(formName) {
            this.$refs[formName].validate((valid) => {
              if (valid) {
                //console.log(this.form)
                new Date(this.form.startTime)<new Date(this.form.endTime)?(()=>{
                  !this.form.isNeedPassword&&(()=>{
                    !this.form.livePassword&&this.$message.error('');
                    this.form.livePassword.length==6&&(()=>{
                      this.form.livePassword&&updateLiveType(this.form).then(res=>{
                        console.log(res,777777)
                        res.code?this.$message.error(res.msg):this.$message.success(res.msg)
                      })
                    })();
                    this.form.livePassword.length!=6&&this.$message.error('6')
                  })();
                  this.form.isNeedPassword&&(()=>{
                    updateLiveType(this.form).then(res=>{
                      console.log(res,777777)
                      res.code?this.$message.error(res.msg):this.$message.success(res.msg)
                    })
                  })();
                })():this.$message.error('')
              } else {
                console.log('error submit!!');
                return false;
              }
            });
          },
          getLiveType(){
            getLiveType(this.id).then(res=>{
              res.code?this.$message.error(res.msg):(()=>{
                this.form.id=res.data.id;
                this.form.startTime=res.data.startTime;
                this.form.endTime=res.data.endTime;
                this.form.isNeedPassword=res.data.isNeedPassword ;
                this.form.isPlayBack=res.data.isPlayBack;
                this.form.liveNetRule=res.data.liveNetRule;
                this.form.livePassword=res.data.livePassword;
              })();
              console.log(res,777)
            })
          }
        },
        created() {
          console.log(11111)
          this.$route.query.id&&(()=>{
            this.id=this.$route.query.id;
            this.getLiveType();
          })();
        },
      mounted() {

      }
  }
</script>

<style scoped>

</style>



